// *****************
// * STAY IN TOUCH *
// *****************

.kcls-communications {
	margin-bottom: 2rem;
}

.kcls-communications-content > div {
	display: grid;
	grid-template-columns: repeat(auto-fit, 300px);
	grid-gap: 2rem;
	justify-content: space-around;
	padding: 2rem;
}

.kcls-communications-item {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 2rem;
	border-radius: 15px;
	box-shadow: 15px 15px 15px #071420;
	color: #071420;
	background-color: #fff;
	text-align: center;
}

// .kcls-communications-item:hover {
// 	cursor: pointer;
// 	transform: scale(1.1);
// }

.kcls-communications-image {
	max-width: 100px;
	margin: auto;
}

.kcls-communications-item-heading {
	padding: 1rem 0;
}

.kcls-communications-item-text {
	padding-bottom: 1rem;
}

.kcls-communications-item-button:hover {
	cursor: pointer;
	transform: scale(1.1);
}
