// ****************
// * Profile Card *
// ****************

.kcls-profile-card {
	margin: 2rem;
}

.kcls-profile-cards > div {
	display: grid;
	grid-template-columns: repeat(auto-fit, 325px);
	grid-gap: 2rem;
	justify-content: space-around;
	padding: 2rem;
	align-items: center;
}

.kcls-profile-card {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 2rem;
	border-radius: 15px;
	box-shadow: 15px 15px 15px #071420;
	color: #071420;
	background-color: #fff;
	max-width: 300px;
	text-align: center;
}

.kcls-profile-image {
	border-radius: 50%;
}

.kcls-profile-image img {
	padding: 0.25rem;
	border: solid 3px var(--kcls-blue);
}

.kcls-profile-title {
	padding-bottom: 0.5rem;
}

.kcls-profile-name {
	padding-bottom: 0.5rem;
}

// **********************
// * Leadership Section *
// **********************

.kcls-leadership-content > div {
	display: grid;
	grid-template-columns: repeat(auto-fit, 325px);
}

// **************
// * Action Bar *
// **************

.kcls-action-bar {
	width: 100%;
	background-color: var(--kcls-blue);
}

.kcls-action-bar > div {
	width: 80%;
	max-width: 1100px;
	padding: 1rem;
	margin: auto;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.kcls-action-bar-text {
	color: white;
	font-size: 1.25rem;
	font-weight: bold;
}

.kcls-action-bar-cta-button a {
	background-color: var(--kcls-green);
	font-weight: bold;
}
